<template>
  <div class="pages" v-if="index || index === 0">
    <el-button type="primary" size="mini" @click="prev" v-if="index > 0" icon="el-icon-arrow-left">上一题</el-button>
    <el-button type="primary" size="mini" @click="next" v-if="index < len - 1">下一题<i class="el-icon-arrow-right el-icon--right"></i></el-button>
    <el-button v-if="!studyMode" type="primary" size="mini" @mouseenter.native="showAns = true" @mouseleave.native="showAns = false">{{ showAns ? '隐藏答案' : '查看答案' }}</el-button>
    <p>
      <el-input align="center" class="jump-input" type="text" size="mini" v-model="targetIndex"></el-input>
      <el-button class="jump-btn" type="primary" size="mini" @click="jump">GO! </el-button>
    </p>
    <p v-if="showAns">{{ Ans }}</p>
    <slot/>
  </div>
</template>

<script>
export default {
  name: 'Controller',
  props: {
    currentIndex: Number,
    len: Number,
    studyMode: Boolean,
    Ans: String
  },
  data () {
    return {
      showAns: false,
      targetIndex: 1,
      index: null
    }
  },
  methods: {
    next () {
      this.index++
      this.updateIndex()
    },
    prev () {
      this.index--
      this.updateIndex()
    },
    jump () {
      let target = this.targetIndex - 1
      if (target >= 0 && target < this.len) {
        this.index = target
      }
      this.updateIndex()
    },
    updateIndex () {
      this.$emit('changeIndex', this.index)
      this.updateTargetIndex()
    },
    updateTargetIndex () {
      this.targetIndex = parseInt(this.index) + 1
    }
  },
  watch: {
    currentIndex (val) {
      this.index = val
      this.updateTargetIndex()
    }
  },
  mounted () {
    this.index = this.currentIndex
  }
}
</script>

<style lang="scss" scoped>
.jump-input{
  width: 60px;
}
.jump-btn{
  margin-left: 10px;
}

@media screen and (max-width: 768px) {
  .jump-input{
    width: 60px;
  }
  .jump-btn{
    margin-left: 10px;
  }
}
</style>
