<template>
  <div v-if="current" class="topics">
    <top-info
      :title="title"
      :currentIndex="currentIndex"
      :len="topics.length"
      :studyMode="studyMode"
      @toggleStudyMode="studyMode = !studyMode"
    />
    <p v-if="showLastIndex">上次学到第 <span class="blue">{{lastIndex + 1}}</span> 题
    <span class="blue" @click="proceed" type="text" v-if="lastIndex">继续学习 <i class="el-icon-arrow-right el-icon--right"></i></span></p>
    <p class="dry">{{current.dry}}</p>
    <ul class="chooses">
      <li
        :class="[ans === index ? 'choosed' : '', Number.isInteger(ans) || studyMode ? 'done' : '', current.ans === ansMap[index] ? 'trueAns' : '']"
        @click="handleChoose(index)"
        v-for="(choose, index) in current.chooses"
        :key="choose"
      >
      <p>{{ `${ansMap[index]}. ${choose}` }}</p>
    </li>
    </ul>
    <controller
      @changeIndex="changeIndex" 
      :currentIndex="currentIndex"
      :studyMode="studyMode"
      :Ans="current.ans"
      :len="topics.length"
    />
    <p>历史错题：</p>
    <span class="blue" v-for="item in worryList" :key="item" @click="changeIndex(item)">
      第{{ item + 1 }}题 
    </span>
  </div>
</template>

<script>
import Controller from '@/components/Controller'
import TopInfo from '@/components/TopInfo'

export default {
  name: 'Choose',
  components: { Controller, TopInfo },
  props: {
    topics: Array,
    title: {
      type: String,
      default: '选择题'
    }
  },
  data () {
    return {
      currentIndex: 0,
      current: null,
      ansMap: 'ABCD',
      ans: null,
      studyMode: false,
      lastIndex: null,
      worryList: [],
      hasWorry: false
    }
  },
  mounted () {
    if (this.topics) {
      this.current = this.topics[this.currentIndex]
    }
    const lastIndex = localStorage.getItem(`${this.title}lastIndex`)
    if (lastIndex) {
      this.lastIndex = parseInt(lastIndex)
    }

    const worryList = localStorage.getItem(`${this.title}worryList`)
    this.worryList = worryList.split(',').map(n => parseInt(n)).filter(n => Number.isInteger(n))
  },
  computed: {
    showLastIndex () {
      return this.lastIndex && (this.currentIndex !== this.lastIndex)
    }
  },
  methods: {
    changeIndex (val) {
      this.currentIndex = val
    },
    handleChoose (index) {
      this.ans = index
      if (this.current.ans === this.ansMap[index]) {
        this.$message({
          duration: 1000,
          type: 'success',
          message: '回答正确，自动进入下一题',
          offset: 0
        })
        const worryListIndex = this.worryList.indexOf(this.currentIndex)
        if (worryListIndex > -1 && !this.hasWorry) {
          this.worryList.splice(worryListIndex, 1)
          localStorage.setItem(`${this.title}worryList`, this.worryList)
        }
        this.currentIndex++
      } else {
        this.hasWorry = true
        if (!this.worryList.includes(this.currentIndex)) {
          this.worryList.push(this.currentIndex)
          localStorage.setItem(`${this.title}worryList`, this.worryList)
        }
      }
    },
    proceed () {
      this.currentIndex = this.lastIndex
    }
  },
  watch: {
    currentIndex () {
      this.current = this.topics[this.currentIndex]
      this.ans = null
      this.lastIndex = null
      this.hasWorry = false
      localStorage.setItem(`${this.title}lastIndex`, this.currentIndex)
    },
    topics () {
      this.current = this.topics[this.currentIndex]
    }
  }
}
</script>

<style lang="scss" scoped>
.topics{
  .dry{
    font-size: 16px;
    white-space: pre-wrap;
  }
  .chooses {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    li {
      cursor: pointer;
      padding: 8px;
      p{
        margin: 0;
        font-size: 16px;
      }
    }
    .choosed {
      color: #E6A23C;
    }
    .done.trueAns {
      color: #67C23A;
    }
  }
}
</style>
